$.widget("ui.autocomplete", $.ui.autocomplete, {
  options: {
    scope: undefined,
    appendTo: null,
    autoFocus: false,
    delay: 300,
    minLength: 1,
    position: {
      my: "left top",
      at: "left bottom",
      collision: "none" },

    source: null,

    // callbacks
    change: null,
    close: null,
    focus: null,
    open: null,
    response: null,
    search: null } });



/**
                        * Extend ui.bizagi_multiselect widget in order to provide async list of resources
                        */
$.widget("ui.bizagi_searchMultiSelect", $.ui.bizagi_multiSelect, {
  options: {
    asyncList: undefined,
    scope: undefined,
    label: "MyLabel",
    _dataItems: [],
    _config: {
      _template: $.bizagi.ui.controls.uimultiselect.tmpl,
      _cssClass: $.bizagi.ui.controls.uimultiselect.css,
      _namespace: $.bizagi.ui.controls.uimultiselect.namespace },

    SEARCH_MIN_LENGTH: 3,
    iconClass: "",
    onChange: new Function(),
    parentPositionElement: "",
    parentElementToFitWidth: "",
    parentElementToBorder: "",
    classOpenedList: "",
    classOpenedParent: "",
    classItem: "",
    openOnFocus: false,
    customRenderItem: null,
    classMenuList: "" },

  _search: function _search(term, response) {
    var self = this;
    self.options.scope.searchMultiSelectScope.options.asyncList(term, response, self.options.scope);
  },
  _create: function _create() {

    var self = this;
    var $element = self.element;

    self.options._dataItems = [];

    $.when(self._renderContent()).done(function (content) {

      $element.append(content);
      var $input = $element.find('input[type=text]');
      self.options.scope.searchMultiSelectScope = self;
      var lastSelectedValue;
      $input.autocomplete({
        scope: self.options.scope,
        source: self._search,
        minLength: self.options.SEARCH_MIN_LENGTH,
        messages:
        {
          noResults: null },

        select: function select(event, ui) {
          var value = $.grep(self.options.list, function (obj) {
            return self._itemValue(obj) == ui.item.id;
          });

          self._prependElements(ui.item);
          lastSelectedValue = ui.item.value;
        },
        close: function close() {
          if (lastSelectedValue && $input.val() == lastSelectedValue.toString())
          $input.val("");
          if (self.options.parentElementToBorder != "" && self.options.classOpenedList != "" && self.options.classOpenedParent != "") {
            $(".ui-menu").removeClass(self.options.classOpenedList);
            $(self.options.parentElementToBorder).removeClass(self.options.classOpenedParent);
          }
        },
        open: function open() {
          // TM13900: Avoid the visual effect for suggest on Forms
          if (self.options && !!self.options.classMenuList) {
            $(".ui-menu").addClass(self.options.classMenuList);
          }
          if (self.options.parentElementToFitWidth != "") {
            $(".ui-menu").width($(self.options.parentElementToFitWidth).width() - 4);
          }
          if (self.options.parentElementToBorder != "" && self.options.classOpenedList != "" && self.options.classOpenedParent != "") {
            $(".ui-menu").addClass(self.options.classOpenedList);
            $(self.options.parentElementToBorder).addClass(self.options.classOpenedParent);
          }
          if (bizagi.util.isIE()) {
            var selfAuto = this;
            $(selfAuto).autocomplete('widget').css('z-index', '');
            setTimeout(function () {
              $(selfAuto).autocomplete('widget').css('z-index', 100);
            }, 100);
          }
        },
        position: self.options.parentPositionElement != "" ? { of: self.options.parentPositionElement, my: "left+0 top-4" } : { my: "left top", at: "left bottom", collision: "none" },
        customRenderItem: self.options.customRenderItem });


      if (self.options.openOnFocus) {
        $input.bind('focus', function () {$(this).autocomplete("search");});
      }

      if (self.options.iconClass != "") {
        $("<i class='" + self.options.iconClass + "'></i>").insertBefore($input);
      }

      self._eventHandlers();
    });

  },

  __getDataSource: function __getDataSource() {

    var self = this;
    var data = [];
    var def = new $.Deferred();

    if (self.options.asyncList) {
      $.when(self.options.asyncList()).done(function (response) {
        $.each(response, function (key, val) {
          data.push({ label: self._itemText(val), value: self._itemText(val), id: self._itemValue(val) });
        });
        def.resolve(data);
      });
    } else {

      $.each(self.options.list, function (key, val) {
        data.push({ label: self._itemText(val), value: self._itemText(val), id: self._itemValue(val) });
      });
      def.resolve(data);
    }

    return def.promise();
  },

  _prependElements: function _prependElements(value) {

    var self = this;
    var $element = self.element;
    var dataItems = self.options._dataItems;
    var $input = $element.find('input[type=text]');

    var n = $.grep(dataItems, function (obj) {
      return obj.value == self._itemValue(value);
    });

    if (!n.length) {
      dataItems.push({ text: self._itemText(value), value: self._itemValue(value), raw: value });
      $input.parent().before('<li class="selected-item ' + self.options.classItem + '" data-value="' + self._itemValue(value) + '"><label>' + self._itemText(value) + '</label><a class="closebutton"></a></li>');
      if (!self._settingItems) {
        self.element.trigger('addItem');
        self.options.onChange(self.options._dataItems);
      }
    }

  },
  _refresh: function _refresh() {

    var self = this;
    var $input = self.element.find('input[type=text]');

    $input.parent('li').siblings('li').remove();
    self.options._dataItems = [];
    self.options.onChange(self.options._dataItems);
  } });