(function ($) {

  $.widget("ui.autocomplete", $.ui.autocomplete, {
    _originalRenderItem: $.ui.autocomplete.prototype._renderItem,
    _renderItem: function _renderItem(ul, item) {
      if (this.options.customRenderItem !== undefined && this.options.customRenderItem != null && typeof this.options.customRenderItem == "function") {
        return this.options.customRenderItem(ul, item);
      }
      return this._originalRenderItem.apply(this, arguments);
    },
    _renderMenu: function _renderMenu(ul, items) {
      var that = this;
      // TM13900: Avoid the visual effect for suggest on Forms
      if (this.options && !!this.options.classMenuList) ul.addClass(this.options.classMenuList);

      $.each(items, function (index, item) {
        that._renderItemData(ul, item);
      });
      if (bizagi.localization.isRTL) {
        ul.addClass("ui-rtl");
      }
    } });


  $.widget('ui.bizagi_multiSelect', {
    options: {
      list: [],
      asyncList: undefined,
      label: "MyLabel",
      _dataItems: [],
      _config: {
        _template: $.bizagi.ui.controls.uimultiselect.tmpl,
        _cssClass: $.bizagi.ui.controls.uimultiselect.css,
        _namespace: $.bizagi.ui.controls.uimultiselect.namespace },

      SEARCH_MIN_LENGTH: 3,
      iconClass: "",
      onChange: new Function(),
      parentPositionElement: "",
      parentElementToFitWidth: "",
      parentElementToBorder: "",
      classOpenedList: "",
      classOpenedParent: "",
      classItem: "",
      openOnFocus: false,
      customRenderItem: null,
      maxDataItems: undefined,
      errorMaxDataItems: undefined,
      notifierFunction: undefined,
      placeholder: undefined,
      classMenuList: "" },

    _create: function _create() {

      var self = this;
      var $element = self.element;

      self.options._dataItems = [];

      $.when(self._renderContent()).done(function (content) {

        $element.append(content);
        var $input = $element.find('input[type=text]');
        if (self.options.placeholder)
        $input.attr("placeholder", self.options.placeholder);

        var lastSelectedValue;
        $input.autocomplete(
        {
          source: self._getDataSource(),
          minLength: self.options.SEARCH_MIN_LENGTH,
          classMenuList: self.options.classMenuList,
          messages:
          {
            noResults: null },

          select: function select(event, ui) {
            var value = $.grep(self.options.list, function (obj) {
              return self._itemValue(obj) == ui.item.id;
            });

            self._prependElements(value[0]);
            lastSelectedValue = ui.item.value;
          },
          close: function close() {
            if (lastSelectedValue && $input.val() == lastSelectedValue.toString())
            $input.val("");
            if (self.options.parentElementToBorder != "" && self.options.classOpenedList != "" && self.options.classOpenedParent != "") {
              $(".ui-menu").removeClass(self.options.classOpenedList);
              $(self.options.parentElementToBorder).removeClass(self.options.classOpenedParent);
            }
          },
          open: function open() {
            if (self.options.parentElementToFitWidth != "") {
              $(".ui-menu").width($(self.options.parentElementToFitWidth).width() - 4);
            }
            if (self.options.parentElementToBorder != "" && self.options.classOpenedList != "" && self.options.classOpenedParent != "") {
              $(".ui-menu").addClass(self.options.classOpenedList);
              $(self.options.parentElementToBorder).addClass(self.options.classOpenedParent);
            }
            if (bizagi.util.isIE()) {
              var selfAuto = this;
              $(selfAuto).autocomplete('widget').css('z-index', '');
              setTimeout(function () {
                $(selfAuto).autocomplete('widget').css('z-index', 100);
              }, 100);
            }
          },
          position: self.options.parentPositionElement != "" ? { of: $(self.options.parentPositionElement), my: "left+0 top-4" } : { my: "left top", at: "left bottom", collision: "none" },
          customRenderItem: self.options.customRenderItem });


        if (self.options.openOnFocus) {
          $input.bind('focus', function () {$(this).autocomplete("search");});
        }

        if (self.options.iconClass != "") {
          $("<i class='" + self.options.iconClass + "'></i>").insertBefore($input);
        }

        self._eventHandlers();
      });

    },
    getDataItems: function getDataItems() {
      var self = this;
      return self.options._dataItems;

    },
    setDataItems: function setDataItems(items) {
      var self = this;

      setTimeout(function () {
        self._settingItems = true;
        $.each(items, function (i, item) {
          self._prependElements(item);
        });
        self._settingItems = false;
      }, 100);
    },
    setList: function setList(list) {

      var self = this;
      self.options.list = list;

      self._refresh();
    },
    getList: function getList() {

      var self = this;
      return self.options.list;
    },
    _initializeTemplates: function _initializeTemplates() {

      var self = this;
      var tmpl = self.options._config._template;

      return {
        "multiselect": bizagi.getTemplate(tmpl) };

    },
    _renderContent: function _renderContent() {

      var self = this;
      var $element = self.element;
      var tmpl = self.options._config._template;
      var css = self.options._config._cssClass;
      var label = self.options.label;

      $element.addClass(css);
      self.loader = new bizagi.ui.controls.tmplloader();

      return $.when(self.loader.loadTemplates(self._initializeTemplates())).pipe(function () {
        var tmpl = self.loader.getTemplate("multiselect");
        return $.tmpl(tmpl, { label: label });
      });
    },
    _refresh: function _refresh() {

      var self = this;
      var $input = self.element.find('input[type=text]');

      $input.parent('li').siblings('li').remove();
      self.options._dataItems = [];
      self.options.onChange(self.options._dataItems);
      $input.autocomplete("option", "source", self._getDataSource());
    },
    _getDataSource: function _getDataSource() {

      var self = this;
      var data = [];

      $.each(self.options.list, function (key, val) {
        data.push({ label: self._itemText(val), value: val, id: self._itemValue(val) });
      });

      return data;
    },
    _prependElements: function _prependElements(value) {

      var self = this;
      var $element = self.element;
      var dataItems = self.options._dataItems;
      var $input = $element.find('input[type=text]');

      var n = $.grep(dataItems, function (obj) {
        return obj.value == self._itemValue(value);
      });

      if (!n.length) {
        if (self.options.maxDataItems !== undefined && dataItems.length == self.options.maxDataItems) {
          self.options.notifierFunction(self.options.errorMaxDataItems);
          return;
        }
        dataItems.push({ text: self._itemText(value), value: self._itemValue(value) });
        $input.parent().before('<li class="selected-item ' + self.options.classItem + '" data-value="' + self._itemValue(value) + '"><label>' + self._itemText(value) + '</label><a class="closebutton"></a></li>');
        if (!self._settingItems) {
          self.element.trigger('addItem');
          self.options.onChange(self.options._dataItems);
        }
      }

    },
    _itemValue: function _itemValue(obj) {

      var self = this;
      var val = '';
      if (self.options.itemValue) {
        val = self.options.itemValue(obj);
      } else {
        if (typeof obj.value === 'string' || typeof obj.value === 'number') {
          val = obj.value;
        }
      }
      return val;
    },
    _itemText: function _itemText(obj) {

      var self = this;
      var val = '';
      if (self.options.itemText) {
        val = self.options.itemText(obj);
      } else {
        if (typeof obj.text === 'string') {
          val = obj.text;
        }
      }
      return val;
    },
    _destroy: function _destroy() {

      var self = this;
      self.options._dataItems = [];
      self.options.onChange(self.options._dataItems);
    },
    _eventHandlers: function _eventHandlers() {

      var self = this;
      var $element = self.element;
      var $input = $element.find('input[type=text]');

      $("ul", $element).on("click", "a.closebutton", function (event) {

        event.preventDefault();

        var value = $(event.target).closest("li").data('value');

        var position = $.map(self.options._dataItems, function (obj, i) {
          if (obj.value == value) {return i;}
        });

        self.options._dataItems.splice(position[0], 1);
        self.options.onChange(self.options._dataItems);
        $(this).parent().remove();

        self.element.trigger('removeItem');
        return false;
      });

      $element.on("click", function (event) {
        $input.focus();
      });

      $input.on("keydown", function (event) {

        if (event.keyCode == 13) {

          event.preventDefault();
        }

      });

    } });


})(jQuery);