/**
* Name: BizAgi Desktop Widget Users Table
* 
* @author Christian Collazos
*/


bizagi.workportal.widgets.userstable.extend("bizagi.workportal.widgets.userstable", {

  SELECTORS: {
    WRAPPER_LOADER: "#biz-wp-userstable-wrapperload",
    WRAPPER_LIST: "#biz-wp-userstable-wrapperlist" } },


{
  init: function init(workportalFacade, dataService, params) {
    var self = this;
    self._super(workportalFacade, dataService, params);

    self.filterData = {};
    self.maxRows = 8;
    self.maxPages = 10;
    self.$canvas = params.canvas;
    self.showBackButton = params.showBackButton;
    self.users = [];
    self.totalPages = 0;
    self.currentPage = 1;
    self.orderType = "ASC";
    self.orderField = "";
    self.tableHeaders = [
    { "displayName": self.getResource("workportal-widget-usertable-id"), "fieldValue": "idUser" },
    { "displayName": self.getResource("workportal-widget-usertable-user-name"), "fieldValue": "userName" },
    { "displayName": self.getResource("workportal-widget-usertable-full-name"), "fieldValue": "fullName" },
    { "displayName": self.getResource("workportal-widget-usertable-domain"), "fieldValue": "domain" },
    { "displayName": self.getResource("workportal-widget-usertable-email"), "fieldValue": "contactEmail" },
    { "displayName": self.getResource("workportal-widget-usertable-active-assign"), "fieldValue": "enabledForAssignation" },
    { "displayName": self.getResource("workportal-widget-usertable-active-column"), "fieldValue": "enabled" }];


    self.loadTemplates({
      "usersTable.wrapper": bizagi.getTemplate("bizagi.workportal.desktop.widgets.userstable").concat("#ui-bizagi-workportal-widget-usersTable-wrapper"),
      "usersTable.form": bizagi.getTemplate("bizagi.workportal.desktop.widgets.userstable").concat("#ui-bizagi-workportal-widget-usersTable-form"),
      "usersTable.table": bizagi.getTemplate("bizagi.workportal.desktop.widgets.userstable").concat("#ui-bizagi-workportal-widget-usersTable-table"),
      "usersTable.message": bizagi.getTemplate("bizagi.workportal.desktop.widgets.userstable").concat("#ui-bizagi-workportal-widget-usersTable-message"),
      useNewEngine: false });


  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("usersTable.wrapper");
    var content;

    content = self.content = $.tmpl(template, {});

    // Override canvas if it has been defined
    if (self.params.canvas) {
      content = $(self.params.canvas).append(content);
    }

    return content;
  },

  postRender: function postRender() {
    var self = this;

    //load templates
    self.loadtemplates();

    //load form data
    self.setupData();
  },

  /*
     * Load template vars
     */
  loadtemplates: function loadtemplates() {
    var self = this;

    self.usersTableFormTmpl = self.getTemplate("usersTable.form");
    self.usersTableTmpl = self.getTemplate("usersTable.table");
    self.usersTableMessage = self.getTemplate("usersTable.message");
  },

  /**
      * make request of get Organizations list depends of widget name in params
      * @returns request
      */
  _makeRequestOrganizationsList: function _makeRequestOrganizationsList() {
    var self = this,
    _fromAdminCaseSearch = self.params.widgetName === self.Class.BIZAGI_WORKPORTAL_WIDGET_ADMIN_CASE_SEARCH,
    _fromReassign = self.params.widgetName === self.Class.BIZAGI_WORKPORTAL_WIDGET_REASSIGN_CASE,
    _forReassign = _fromAdminCaseSearch || _fromReassign,
    _isUserAdmin = true;

    return _forReassign ? self.dataService.getOrganizationsForReassignCases() : self.dataService.getOrganizationsList(_isUserAdmin);
  },

  /*
     * Setup form data
     */
  setupData: function setupData() {
    var self = this;

    $.when(self._makeRequestOrganizationsList()).done(function (rps) {

      var orgList = rps.response;

      var resultTemplate = $.tmpl(self.usersTableFormTmpl, { orgList: orgList.length, showBackButton: self.showBackButton });
      $("#biz-wp-userstable-wrapperform", self.content).html(resultTemplate);

      $("#btn-users-table-back", self.content).click(function (e) {
        if (self.params.parentDef) {
          self.params.parentDef.resolve();
        }
      });

      if (orgList.length) {
        orgList.unshift({ displayName: "----------------", id: "" });
        self.setupOrgCombo(resultTemplate, orgList);
      }

      self.setupEventsHandler();
      if (self.$canvas === undefined) {
        self.$canvas = self.content;
      }
      self.$canvas.trigger("completeUserForm");

    }).fail(function (error) {
      bizagi.log(error);
    });

  },

  /*
     * Setup organization combo
     */
  setupOrgCombo: function setupOrgCombo($content, data) {

    var self = this;
    var $combo = $content.find("#biz-wp-usertable-org");

    $combo.uicombo({
      data: { combo: data },
      initValue: data[0],
      itemValue: function itemValue(item) {
        return item.id;
      },
      itemText: function itemText(item) {
        return item.displayName;
      } });


  },

  /*
     * Implement main buttons behaviour
     */
  setupEventsHandler: function setupEventsHandler() {
    var self = this,
    content = self.getContent();
    var $userForm = $("#biz-wp-userstable-wrapperform form", content);

    $userForm.on("submit", function (e) {

      e.preventDefault();
      if (typeof self.afterSave == "undefined") self.currentPage = 1;else
      {
        delete self.afterSave;
        self.currentPage = self.currentPage || 1;
      }
      self.orderType = "ASC";
      self.orderField = "";

      self.filterData = {
        domain: this.elements[0].value,
        userName: this.elements[1].value,
        fullName: this.elements[2].value,
        organization: $(this.elements[3]).data('value'),
        pag: self.currentPage,
        pagSize: self.maxRows,
        orderField: self.orderField,
        orderType: self.orderType };


      self.getUsersTableData();

    });

    $userForm.on("reset", function (event) {

      event.preventDefault();

      $(this.elements[3]).data("value", "").val("----------------");
      this.elements[0].value = "";
      this.elements[1].value = "";
      this.elements[2].value = "";

      $("#biz-wp-userstable-wrapperlist", content).empty();
    });

  },

  /*
     * Clean Form
     */
  cleanForm: function cleanForm() {

    var self = this,
    content = self.getContent();
    var $userForm = $("#biz-wp-userstable-wrapperform form", content);

    $userForm.trigger('reset');
  },

  /*
     * Excecute Query 
     */
  excecuteQuery: function excecuteQuery() {

    var self = this;
    content = self.getContent();
    var $userForm = $("#biz-wp-userstable-wrapperform form", content);

    $userForm.trigger('submit');
  },

  /**
      * show loader while list request is in progress
      * @param {Deferred} defer on finish loader ends
      */
  _loadingList: function _loadingList(defer) {
    var self = this,
    _SELECTORS = this.Class.SELECTORS,
    $list = $(_SELECTORS.WRAPPER_LIST, self.content),
    $loader = $(_SELECTORS.WRAPPER_LOADER, self.content),
    _start = function _start() {
      $loader.show();
      $list.hide();
    },
    _end = function _end() {
      $loader.hide();
      $list.show();
    };

    _start();
    defer.always(_end);
  },

  /**
      * make the request for get users
      * @param {JSON} params 
      * @returns request
      */
  _makeRequestGetUsers: function _makeRequestGetUsers(params) {
    var self = this,
    _calcRequestMethod = function _calcRequestMethod() {
      var method;
      switch (self.params.widgetName) {
        case self.Class.BIZAGI_WORKPORTAL_WIDGET_REASSIGN_CASE:
        case self.Class.BIZAGI_WORKPORTAL_WIDGET_ADMIN_CASE_SEARCH:
          method = self.dataService.getUsersForReassignCases;
          break;

        case self.Class.BIZAGI_WORKPORTAL_WIDGET_ADMIN_USER_PROFILES:
          method = self.dataService.getUsersForAssignation;
          break;

        case self.Class.BIZAGI_WORKPORTAL_WIDGET_ADMIN_DEFAULTS_ASSIGNATION_USER:
          method = self.dataService.getUsersForDefaultAssignation;
          break;

        case self.Class.BIZAGI_WORKPORTAL_WIDGET_ADMIN_USERS_ADMINISTRATION:
          method = self.dataService.getUsersForUsersAdministration;
          break;

        case self.Class.BIZAGI_WORKPORTAL_WIDGET_QUERIES_MANAGEMENT:
          method = self.dataService.getUsersForQueriesPermissionManager;
          break;

        default:
          method = self.dataService.getUsersList;
          break;}

      return method;
    };

    return _calcRequestMethod().call(self.dataService, params);
  },

  /*
     * get users table data
     */
  getUsersTableData: function getUsersTableData(params) {
    var self = this;

    var params = params || {};
    self.orderType = params.orderType || self.orderType;
    self.orderField = params.orderField || self.orderField;
    var defer = self._makeRequestGetUsers($.extend(self.filterData, params));
    self._loadingList(defer);
    $.when(defer).done(function (data) {
      self.fillUserInformation(data);
    }).fail(function (error) {
      bizagi.log(error);
    });
  },

  /*
     * if users returned show the table
     */
  showUsersTable: function showUsersTable() {

    var self = this;
    var $wrapperList = $("#biz-wp-userstable-wrapperlist", self.content);

    $wrapperList.empty();

    if (self.users.length > 0) {

      var pagination = self.totalPages > 1 ? true : false;
      var pagesArray = self.getPagesArray();

      var links = self.params.userLinkLabel && Object.prototype.toString.apply(self.params.userLinkLabel) === "[object Array]" ? self.params.userLinkLabel : [self.params.userLinkLabel];

      var usersTableTmpl = $.tmpl(self.usersTableTmpl, { users: self.users, headers: self.tableHeaders, links: links, page: self.currentPage, pagination: pagination, pagesArray: pagesArray, orderField: self.orderField, orderType: self.orderType });
      $wrapperList.html(usersTableTmpl);

      self.setUpPagination();
      self.setUpSortColumnLinks();
    } else {

      var msg = bizagi.localization.getResource("workportal-widget-usertable-empty");
      $wrapperList.html($.tmpl(self.usersTableMessage, { message: msg }));
    }
  },

  /*
     * Get Pages
     */
  getPagesArray: function getPagesArray() {

    var self = this;
    var pagesToShow = self.maxPages > self.totalPages ? self.totalPages : self.maxPages;
    var aux = [];

    for (var a = 0; a < pagesToShow; a++) {
      aux.push(a + 1);
    }

    return aux;
  },

  /*
     * Set pagination
     */
  setUpPagination: function setUpPagination() {
    var self = this;
    var $pager = $("#biz-wp-userstable-pager ul", self.content);

    $pager.bizagiPagination({
      maxElemShow: self.maxRows,
      totalPages: self.totalPages,
      actualPage: self.currentPage,
      listElement: $pager,
      clickCallBack: function clickCallBack(options) {
        self.filterData.pag = self.currentPage = parseInt(options.page);
        self.getUsersTableData();
      } });


  },

  /*
     * assign click event to each column to order
     */
  setUpSortColumnLinks: function setUpSortColumnLinks() {
    var self = this;

    $(".biz-wp-table-head th", self.content).click(function () {

      var orderField = $(this).data("orderfield");
      var orderType = $(this).data("ordertype");

      self.getUsersTableData({ "orderType": orderType, "orderField": orderField });
      self.assignEventReassign();
    });

  },

  assignEventReassign: function assignEventReassign() {
    var self = this;

    $(".bizagi-wp-userstable-userlink", self.content).click(function (e) {
      var that = this;
      if (self.params.parentDef) {
        var done = function done() {
          $(document).data('auto-save', '');
          self.params.parentDef.resolve(that.id, that.name, that.attributes.guid.value);
          if (self.params.widgetName == self.Class.BIZAGI_WORKPORTAL_WIDGET_REASSIGN_CASE && bizagi.workportal.desktop.dialogStack.length > 0)
          bizagi.workportal.desktop.dialogStack[bizagi.workportal.desktop.dialogStack.length - 1].dialogBox.on("dialogbeforeclose", function (event, ui) {
            // Next variable specifies if is redirecting to inbox when the result status does not have errors.
            if (self.resultStatus === true) {
              self.publish("changeWidget", {
                widgetName: bizagi.workportal.currentInboxView });

            }
          });
        };
        var fail = function fail() {
          self.params.data.closeDialogWidget();
        };
        $.when(bizagi.util.autoSave()).done(done).fail(fail);
      } else {
        // publish executes twice when create a new instance of userstable
        self.$canvas.trigger("clickUserLink", { id: this.id, name: this.name, fullName: this.getAttribute("fullname"), type: $(this).data("linkname") });
      }
    });
  },

  /*
     * sort table by a given column
     */
  sortUsersTableByColumn: function sortUsersTableByColumn(data, column, orderType) {

    return bizagi.util.sortJSON(data, column, orderType);

  },

  fillUserInformation: function fillUserInformation(data) {
    var self = this;
    self.users = data.users;
    self.totalPages = data.total;

    self.showUsersTable();
    self.assignEventReassign();
  } });