var nicEditorButton = bkClass.extend({

  construct: function construct(e, buttonName, options, nicEditor) {
    this.options = options.buttons[buttonName];
    this.name = buttonName;
    this.ne = nicEditor;
    this.elm = e;

    this.margin = new bkElement('DIV').setStyle({
      'float': 'left',
      marginTop: '2px' }).
    appendTo(e);
    this.contain = new bkElement('DIV').setStyle({
      width: '20px',
      height: '20px' }).
    addClass('buttonContain').appendTo(this.margin);
    this.border = new bkElement('DIV').setStyle({
      backgroundColor: '#efefef',
      border: '1px solid #efefef' }).
    appendTo(this.contain);
    this.button = new bkElement('DIV').setStyle({
      width: '18px',
      height: '18px',
      overflow: 'hidden',
      zoom: 1,
      cursor: 'pointer' }).
    addClass('button').setStyle(this.ne.getIcon(buttonName, options)).appendTo(this.border);
    this.button.addEvent('mouseover', this.hoverOn.closure(this)).addEvent('mouseout', this.hoverOff.closure(this)).addEvent('mousedown', this.mouseClick.closure(this)).noSelect();

    if (!window.opera) {
      this.button.onmousedown = this.button.onclick = bkLib.cancelEvent;
    }

    nicEditor.addEvent('selected', this.enable.closure(this)).addEvent('blur', this.disable.closure(this)).addEvent('key', this.key.closure(this));

    this.disable();
    this.init();
  },

  init: function init() {},

  hide: function hide() {
    this.contain.setStyle({
      display: 'none' });

  },

  updateState: function updateState() {
    if (this.isDisabled) {
      this.setBg();
    } else
    if (this.isHover) {
      this.setBg('hover');
    } else
    if (this.isActive) {
      this.setBg('active');
    } else
    {
      this.setBg();
    }
  },

  setBg: function setBg(state) {
    switch (state) {
      case 'hover':
        var stateStyle = {
          border: '1px solid #666',
          backgroundColor: '#ddd' };

        break;
      case 'active':
        var stateStyle = {
          border: '1px solid #666',
          backgroundColor: '#ccc' };

        break;
      default:
        var stateStyle = {
          border: '1px solid #efefef',
          backgroundColor: '#efefef' };}


    this.border.setStyle(stateStyle).addClass('button-' + state);
  },

  checkNodes: function checkNodes(e) {
    var elm = e;
    do {
      if (this.options.tags && bkLib.inArray(this.options.tags, elm.nodeName)) {
        this.activate();
        return true;
      }
    } while (elm = elm.parentNode && elm.className != "nicEdit");
    elm = $BK(e);
    while (elm.nodeType == 3) {
      elm = $BK(elm.parentNode);
    }
    if (this.options.css) {
      for (itm in this.options.css) {
        if (elm.getStyle(itm, this.ne.selectedInstance.instanceDoc) == this.options.css[itm]) {
          this.activate();
          return true;
        }
      }
    }
    this.deactivate();
    return false;
  },

  activate: function activate() {
    if (!this.isDisabled) {
      this.isActive = true;
      this.updateState();
      this.ne.fireEvent('buttonActivate', this);
    }
  },

  deactivate: function deactivate() {
    this.isActive = false;
    this.updateState();
    if (!this.isDisabled) {
      this.ne.fireEvent('buttonDeactivate', this);
    }
  },

  enable: function enable(ins, t) {
    this.isDisabled = false;
    this.contain.setStyle({
      'opacity': 1 }).
    addClass('buttonEnabled');
    this.updateState();
    this.checkNodes(t);
  },

  disable: function disable(ins, t) {
    this.isDisabled = true;
    this.contain.setStyle({
      'opacity': 0.6 }).
    removeClass('buttonEnabled');
    this.updateState();
  },

  toggleActive: function toggleActive() {
    this.isActive ? this.deactivate() : this.activate();
  },

  hoverOn: function hoverOn() {
    if (!this.isDisabled) {
      this.isHover = true;
      this.updateState();
      this.ne.fireEvent("buttonOver", this);
    }
  },

  hoverOff: function hoverOff() {
    this.isHover = false;
    this.updateState();
    this.ne.fireEvent("buttonOut", this);
  },

  mouseClick: function mouseClick() {
    if (this.options.command) {
      this.ne.nicCommand(this.options.command, this.options.commandArgs);
      if (!this.options.noActive) {
        this.toggleActive();
      }
    }
    this.ne.fireEvent("buttonClick", this);
  },

  key: function key(nicInstance, e) {
    if (this.options.key && e.ctrlKey && String.fromCharCode(e.keyCode || e.charCode).toLowerCase() == this.options.key) {
      this.mouseClick();
      if (e.preventDefault) e.preventDefault();
    }
  } });




